'use strict';

var fetch = global.fetch || require('node-fetch');

var parse = require('@xmpp/xml/lib/parse');

var compareAltConnections = require('./alt-connections').compare;

function resolve(domain) {
  return fetch("https://".concat(domain, "/.well-known/host-meta")).then(function (res) {
    return res.text();
  }).then(function (res) {
    return parse(res).getChildren('Link').filter(function (link) {
      return ['urn:xmpp:alt-connections:websocket', 'urn:xmpp:alt-connections:httppoll', 'urn:xmpp:alt-connections:xbosh'].includes(link.attrs.rel);
    }).map(function (_ref) {
      var attrs = _ref.attrs;
      return {
        rel: attrs.rel,
        href: attrs.href,
        method: attrs.rel.split(':').pop(),
        uri: attrs.href
      };
    }).sort(compareAltConnections);
  }).catch(function () {
    return [];
  });
}

module.exports.resolve = resolve;